.App {
  text-align: center;
}

.back {
  margin: 0.5rem;
}

.heading {
  text-transform: uppercase;
  font-weight: 500;
  letter-spacing: 0.2rem;
  font-size: 1.5rem;
  margin-bottom: 1rem;
}

.iconlink {
  font-size: 1.5rem;
}

.smlinks {
  display: flex;
  justify-content: center;
}

.smlinks > * {
  display: block;
}

.smlinks > *+* {
  margin-left: 0.75rem;
}

#logo {
  max-width: 8rem;
}
